import React, { Component } from "react";
import { connect } from "react-redux";
import {
  getBlogsState,
  getEventState,
  getNavBrandState,
  getNavLinkState,
  getProgramState,
} from "../../store/reselect";
import { push } from "redux-first-history";
import {
  CourseTitle,
  BlogTitle,
  FeatureBody,
  FeaturesBanner,
  FooterBrandBody,
  AdmissionBody,
  BannerTitle,
  BannerBody,
  Desk,
  FeaturesTitle,
} from "../../utils/data";
import { getEventAction, getPostsAction, getProgramsAction } from "../../store/action";
import { withRouter } from "react-router-dom";

import slide1 from "../../assets/cruxcohtech.png";
import slide3 from "../../assets/slider/slide_3.jpg";

import View from "../../presentations/home";

class Home extends Component {
  state = {};

  async componentDidMount() {
    this.props.getPosts();
    this.props.getPrograms();
    this.props.getEvents();
  }

  handleCardClickPost = (id) => {
    this.props.history.push("/news-detail", { id });
  };

  handleCardClickProgram = (id) => {
    this.props.history.push("/program-detail", { id });
  };

  render() {
    let {
      props: { navbrand, navlinks, courses, getEnrolled, blogs, events },
      handleCardClickPost,
      handleCardClickProgram,
    } = this;
    return (
      <View
        desk={Desk}
        events={events}
        headerTitle={BannerTitle}
        headerBody={BannerBody}
        navbrand={navbrand}
        navlinks={navlinks}
        imageUri={slide1}
        featureBody={FeatureBody}
        featuresTitle={FeaturesTitle}
        featuresBanner={FeaturesBanner}
        course={courses}
        onEnroll={getEnrolled}
        onCardClick={handleCardClickPost}
        onCard={handleCardClickProgram}
        blogTitle={BlogTitle}
        blogs={blogs}
        provostSrc={slide3}
        courseTitle={CourseTitle}
        admissionBody={AdmissionBody}
        footerBrandBody={FooterBrandBody}
      />
    );
  }
}

const mapStateToProps = (state) => ({
  navlinks: getNavLinkState(state),
  navbrand: getNavBrandState(state),
  blogs: getBlogsState(state),
  courses: getProgramState(state),
  events: getEventState(state)
});

const mapDispatchToProps = (dispatch) => {
  return {
    getPosts: () => dispatch(getPostsAction()),
    getPrograms: () => dispatch(getProgramsAction()),
    getEnrolled: () => dispatch(push("/contacts")),
    getMorePrograms: () => dispatch(push("/programmes")),
    getMoreBlog: () => dispatch(push("/news")),
    getEvents: () => dispatch(getEventAction()),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Home));
