import React from "react";
import PropTypes from "prop-types";
import altImg from "../../assets/nurse.jpeg";
import logo from "../../assets/logo.jpeg";

const Card = ({ id, img, title, body, children, className, click }) => {
  return (
    <div
      key={id}
      onClick={click}
      data-aos="fade-up"
      className={`col-md-6 col-lg-4 col-xs-12 mt-md-3 card-margin-right-bottom ${className}`}
    >
      <div className="single-course card">
        <div className="position-relative">
          {img ? (
            <img src={img} alt="..." />
          ) : (
            <>
              {" "}
              <img
                className="position-absolute img-fluid card-img-logo"
                src={logo}
                alt="..."
              />
              <img src={altImg} alt="..." />
            </>
          )}
        </div>
        <div className="course-detai p-3">
          <h5 className="app-text">{title}</h5>
          <p className="mt-2"> {body} </p>
          {children}
        </div>
      </div>
    </div>
  );
};

Card.propTypes = {
  id: PropTypes.oneOfType([
    PropTypes.number.isRequired,
    PropTypes.string.isRequired,
  ]),
  img: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  body: PropTypes.string,
  children: PropTypes.element.isRequired,
};

export default Card;
