import React from "react";
import PropTypes from "prop-types";

const Banner = ({ cardDetails }) => {
  const card = cardDetails.map((view) => (
    <div
      data-aos="fade-up"
      key={view.id}
      className="col-md-4 col-lg-4 col-sm-6 col-xs-12"
    >
      <div className="text-box padding30">
        <div className="icon-box">{view.icon}</div>
        <h3 className="title-box">{view.title}</h3>
        <p className="body-box text-black-50 lead">{view.body}</p>
      </div>
    </div>
  ));
  return <div className="row promo">{card}</div>;
};

Banner.propTypes = {
  cardDetails: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.oneOfType([
        PropTypes.number.isRequired,
        PropTypes.string.isRequired,
      ]),
      icon: PropTypes.element,
      title: PropTypes.string,
      body: PropTypes.string,
    })
  ),
};

export default Banner;
