import React, { Fragment } from "react";
import PropTypes from "prop-types";
import Header from "../components/headers";
import Course from "./components/course";
import Feature from "./components/features";
import ProvostDesk from "./components/desk";
import Blog from "./components/blog";
import Event from './components/events';
import Admission from "../components/footer/admission";
import Footer from "../components/footer";

const HomePresentation = ({
  headerTitle,
  headerBody,
  navbrand,
  navlinks,
  imageUri,
  featuresBanner,
  featuresTitle,
  featureBody,
  courseTitle,
  course,
  onEnroll,
  onCard,
  desk,
  events,
  provostSrc,
  blogTitle,
  blogs,
  onCardClick,
  admissionBody,
  footerBrandBody
}) => {
  return (
    <Fragment>
      <Header
        title={headerTitle}
        body={headerBody}
        brand={navbrand}
        navItems={navlinks}
        image={imageUri}
      />
      <Feature
        values={featuresBanner}
        title={featuresTitle}
        body={featureBody}
      />
      <Event dataList={events?.slice(0, 3)} />
      <Course
        sectionTitle={courseTitle}
        dataList={course?.slice(0, 3)}
        onEnroll={onEnroll}
        onCardClick={onCard}
      />
      <ProvostDesk desk={desk} src={provostSrc} />
      <Blog
        sectionTitle={blogTitle}
        dataList={blogs?.slice(0, 3)}
        onCardClick={onCardClick}
      />
      <Admission body={admissionBody} />
      <Footer navItem={navlinks} brandBody={footerBrandBody} />
    </Fragment>
  );
};

HomePresentation.propTypes = {
  headerTitle: PropTypes.string,
  headerBody: PropTypes.string,
  navbrand: PropTypes.string,
  navlinks: PropTypes.array,
  imageUri: PropTypes.string,
  featuresBanner: PropTypes.array,
  featuresTitle: PropTypes.string,
  featureBody: PropTypes.string,
  courseTitle: PropTypes.string,
  course: PropTypes.string,
  onEnroll: PropTypes.func,
  onMorePrograms: PropTypes.func,
  desk: PropTypes.string,
  provostSrc: PropTypes.string,
  blogTitle: PropTypes.string,
  blogs: PropTypes.array,
  onMoreBlogs: PropTypes.func,
  admissionBody: PropTypes.string,
  footerBrandBody: PropTypes.string
};

export default HomePresentation;
