/* eslint-disable no-underscore-dangle */
/* eslint-disable react/jsx-key */
import React from "react";
import PropTypes from "prop-types";
import Card from "../../../../components/card";
import Empty from "../../../error/empty";
import { Link } from "react-router-dom";

const TopCourse = ({ sectionTitle, dataList, onEnroll, onCardClick }) => {
  const course = dataList.map((data) => (
    <Card
      click={() => onCardClick(data._id)}
      key={data._id}
      img={data.img}
      title={data.title}
      body={data.description}
    >
      <p className="mt-4">
        <a href="/contact" onClick={onEnroll} alt="" className="button enroll-button">
          Enroll the Courses
        </a>{" "}
      </p>
    </Card>
  ));
  return (
    <section className="section-padding">
      <div className="container-lg">
        <div className="row">
          <div className="col-md-12 col-xs-12">
            <div data-aos="fade-down" className="text-center mb-5">
              <h2 className="h2 blog-header"> {sectionTitle}</h2>
            </div>
          </div>
        </div>

        <div className="row course-list slide">
          {course?.length ? course : <Empty height="40%" />}
        </div>
        <div className="d-flex justify-content-end">
          {course?.length ? (
            <Link
              to="/programs"
              style={{ color: "#cc8b79" }}
              className="btn mt-5"
            >
              Read more
            </Link>
          ) : null}
        </div>
      </div>
    </section>
  );
};

TopCourse.propTypes = {
  sectionTitle: PropTypes.string.isRequired,
  dataList: PropTypes.arrayOf(
    PropTypes.shape({
      _id: PropTypes.string.isRequired,
      img: PropTypes.string,
      title: PropTypes.string.isRequired,
      body: PropTypes.string,
    })
  ),
  onEnroll: PropTypes.func,
  onMore: PropTypes.func,
};

export default TopCourse;
