import { request } from "../utils/request";
import { toast } from "react-toastify";
import { types } from "../utils/actionGenerator";

export const getPostsAction = (skip) => async (dispatch) => {
  await dispatch({ type: types.post.start });
  try {
    const res = await request({
      url: "/posts",
      params: { skip },
      method: "GET",
    });
    await dispatch({ type: types.post.complete, payload: res.data.payload });
  } catch (error) {
    await dispatch({ type: types.post.error });
    if (error?.response) {
      const message = error?.response?.data?.message;
      await toast(message, {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        type: "error",
      });
    } else if (error?.request) {
      await toast("Network Error", {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        type: "error",
      });
    }
  }
};

