export const contactAction = (data) => async (dispatch) => {
  try {
    await dispatch({ type: types.contact.start });
    const res = await request({
      url: "/mails/create",
      method: "POST",
      data,
    });
    await dispatch({ type: types.contact.complete });
    await toast(res.data.message, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      type: "success",
    });
  } catch (error) {
    await dispatch({ type: types.contact.error });
    await toast("Network Error", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      type: "error",
    });
  }
};


