import { SRLWrapper } from "simple-react-lightbox";
import Empty from "../../../error/empty";

const Events = ({ dataList }) => {
  const eventsImg = dataList.map((data) => (
    <div
      key={data.id}
      style={{ maxHeight: 300, overflow: "hidden" }}
      className="col-md-6 col-lg-4 my-2"
    >
      <SRLWrapper>
        <img className="img-fluid" src={data.imageUrl} alt={data.name} />
      </SRLWrapper>
    </div>
  ));
  return (
    <section className="section-padding">
      <div className="container-lg">
        <div className="row">
          <div className="col-md-12 col-xs-12">
            <div data-aos="fade-down" className="text-center mb-3">
              <h2 className="h2 blog-header"> Events </h2>
            </div>
          </div>
        </div>

        <div className="row course-list mt-4 mb-4">
          {dataList?.length ? eventsImg : <Empty />}
        </div>
      </div>
    </section>
  );
};

export default Events;
