/* eslint-disable no-underscore-dangle */
import React from "react";
import { formatDate } from "../../../../utils/actionGenerator";
import PropTypes from "prop-types";
import Card from "../../../../components/card";
import Empty from "../../../error/empty";
import { Link } from "react-router-dom";

function Blog({ sectionTitle, dataList, onCardClick }) {
  const blogList = dataList.map((data) => (
    <Card
      className="mt-4 mt-sm-0"
      click={() => onCardClick(data?._id)}
      key={data?._id}
      img={data?.imageUrl}
      title={data?.title}
      body={data?.description}
    >
      <small className="blog-meta font14 mt20">
        {` published on ${formatDate(data?.publishAt)}`}
      </small>
    </Card>
  ));
  return (
    <section className="padding-top pb-5">
      <div className="container-lg">
        <div className="row">
          <div className="col-md-12 col-xs-12">
            <div className="text-center mb-5">
              <h2 className="h2 blog-header"> {sectionTitle}</h2>
            </div>
          </div>
        </div>

        <div className="row course-list mt-4 mb-4">
          {dataList?.length ? blogList : <Empty />}
        </div>
        <div className="d-flex justify-content-end">
          {dataList?.length ? (
            <Link to="/news" style={{ color: "#cc8b79" }} className="btn mt-5">
              Read more
            </Link>
          ) : null}
        </div>
      </div>
    </section>
  );
}

Blog.propTypes = {
  dataList: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.oneOfType([
        PropTypes.number.isRequired,
        PropTypes.string.isRequired,
      ]),
      title: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
      publishAt: PropTypes.string.isRequired,
    })
  ),
  sectionTitle: PropTypes.string.isRequired,
  onMore: PropTypes.func,
};

export default Blog;
